@import url(https://fonts.googleapis.com/css2?family=Barlow:ital,wght@0,100;0,200;0,300;0,400;0,600;0,700;1,100;1,200;1,400;1,500;1,700&family=Bitter:ital,wght@0,400;0,500;0,600;1,300;1,600&family=Calligraffitti&family=Electrolize&display=swap);
body {
  font-family: 'Calligraffitti', cursive, sans-serif;
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  background: linear-gradient(to  left top, rgb(165, 255, 255), rgb(252, 161, 235));
  font-weight: normal;
}

.container{
  display: grid;
  justify-content: center;
  align-content: center;
  min-height: 100vh;
  grid-template-columns: repeat(4, 100px);
  grid-template-rows: minmax(120px, auto) repeat(5, 90px);
}

.container > div {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  color: rgb(163, 92, 230);
  font-size: 2rem;
  border: 1px solid rgb(255, 255, 255);
}

.nomber {
  background-color: rgba(250, 249, 243, 0.3);
}

.operator {
  background-color: rgba(243, 208, 232, 0.3);
}

.clean {
  background-color: rgba(255, 46, 64, 0.2);
}

.equal {
  background-color: rgba(159, 202, 236, 0.4);
}

.nomber:hover {
  background-color: rgba(250, 249, 243, 0.9);
}

.operator:hover {
  background-color: rgba(255, 240, 244, 0.9);
}

.clean:hover {
  background-color: rgba(255, 46, 64, 0.4);
}

.equal:hover {
  background-color: rgba(159, 202, 236, 0.8);
}

#clear, #zero {
  grid-column: span 2;
}

#equals {
  grid-row: span 2;
}

#display {
  grid-column: 1/-1;
  border: 1px solid rgb(255, 255, 255);
  background-color: rgba(252, 252, 252, 0.7);
  display: flex;
  align-items: flex-end;
  justify-content: space-around;
  flex-direction: column;
}

#display input {
  width: 100%;
  padding: 5px 20px;
  text-align: right;
  background-color: none;
  border: none;
  color: rgb(185, 89, 177);
}

.total {
  padding: 5px 20px;
}





